.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 100;

    background-color: rgba(0, 0, 0, 0.2);
    opacity: 1;
    transition: opacity 250ms cubic-bezier(0.075, 0.82, 0.165, 1);
    overflow: hidden;
}

.backdrop.is-hidden {
    opacity: 0;
    pointer-events: none;
}

.modal-window {
    width: calc(100% - 40px);
    padding: 48px 20px 40px;
    height: auto;
    max-height: 80vh;
    position: absolute;
    top: 50%;
    left: 50%;
    overflow-y: auto;
    transform: translate(-50%, -50%);
    background: #0C0B0B;
    overflow-y: auto;
    scrollbar-color: #5c5a5a;

    &::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
        border-radius: 10px;
        background-color: #0C0B0B;
    }

    &::-webkit-scrollbar {
        width: 6px;
        background-color: #0C0B0B;
    }

    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
        background-color: #2a2a2a;
    }
}

.modal-window__button {
    position: absolute;
    right: 8px;
    top: 8px;
    width: 30px;
    height: 30px;
    margin: 0;
    padding: 0;
    text-align: center;
    border: none;
    transform: scale(1);
    background-color: transparent;
    transition: transform 250ms cubic-bezier(0.075, 0.82, 0.165, 1);

    &:hover {
        transform: scale(1.25);
    }
}

.modal-card {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
}


@media screen and (min-width: 480px) {

    .modal-window {
        width: 380px;
    }
}


@media screen and (min-width: 768px) {

    .modal-window {
        width: 600px;
        padding: 64px 32px;
    }
}


@media screen and (min-width: 1200px) {

    .modal-window {
        width: 1000px;
    }

    .modal-card {
        flex-wrap: nowrap;
    }
}