@import '~node_modules/modern-normalize/modern-normalize.css';
body {
  font-family: "Poppins";
  font-weight: normal;
  background-color: #0C0B0B;
  margin: 0;
  padding: 0;
}

body.modal-open {
  overflow: hidden;
}

button {
  cursor: pointer;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul, li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

img {
  display: block;
}

a {
  text-decoration: none;
  color: inherit;
}

header {
  margin: 20px;
  margin-bottom: 44px;
}

.logo__icon {
  width: 98px;
  height: 15px;
  transition: transform cubic-bezier(0.075, 0.82, 0.165, 1) 250ms;
}
.logo__icon:hover, .logo__icon:focus {
  transform: scale(1.2);
}

.hero-section,
.random-sentiom {
  margin: 0 20px;
}

.hero-subtitle {
  color: rgba(255, 255, 255, 0.7);
  font-size: 18px;
  font-weight: 400;
  line-height: 160%;
  text-transform: uppercase;
}

.hero-title {
  margin-top: 14px;
  width: 300px;
  color: #FAFAFA;
  font-size: 44px;
  font-weight: 500;
  line-height: 44px;
  letter-spacing: -0.88px;
}

.hero-text {
  margin-top: 16px;
  margin-bottom: 20px;
  color: #FAFAFA;
  width: auto;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}

.all-char-btn {
  padding: 10px 24px;
  border-radius: 1000px;
  font-family: "Poppins";
  color: #FAFAFA;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  text-transform: uppercase;
  transition: color cubic-bezier(0.075, 0.82, 0.165, 1) 250ms, font-size cubic-bezier(0.075, 0.82, 0.165, 1) 250ms;
}
.all-char-btn:hover, .all-char-btn:focus {
  color: #ffffff;
  font-size: 13px;
}

.hero-button {
  background: #34387F;
}

.copyright-section {
  background-color: #34387F;
  width: 100%;
}

.copyright-section__text {
  padding: 16px;
  text-align: center;
  color: rgba(250, 250, 250, 0.4);
  font-size: 10px;
  line-height: 12px;
  letter-spacing: 0.2px;
}

@media screen and (min-width: 480px) {
  .hero-text {
    width: 420px;
  }
}
@media screen and (max-width: 767px) {
  .gallery__item {
    min-width: 75%;
  }
}
@media screen and (min-width: 480px) and (max-width: 767px) {
  .hero-section {
    width: 440px;
    margin-left: auto;
    margin-right: auto;
  }
}
@media screen and (min-width: 768px) {
  header {
    margin: 24px 32px 80px;
  }
  .logo__icon {
    width: 202px;
    height: 31px;
  }
  .hero-section {
    width: 728px;
    margin-left: auto;
    margin-right: auto;
  }
  .random-section {
    width: 728px;
    margin-left: auto;
    margin-right: auto;
  }
  .hero-title {
    margin-top: 16px;
    width: 538px;
    font-size: 80px;
    font-weight: 500;
    line-height: 80px;
    letter-spacing: -1.6px;
  }
  .hero-text {
    margin-top: 18px;
    margin-bottom: 44px;
    width: 473px;
    font-size: 16px;
  }
  .all-char-btn {
    padding: 12px 24px;
    font-size: 14px;
    line-height: 18px;
  }
  .all-char-btn:hover, .all-char-btn:focus {
    font-size: 15px;
  }
  .copyright-section__text {
    padding: 18px;
    font-size: 14px;
    line-height: 18px;
  }
}
@media screen and (min-width: 1200px) {
  .hero {
    width: 510px;
    margin-right: 50px;
  }
  .hero-section {
    width: calc(100% - 64px);
    margin: 0 auto;
    display: flex;
    align-items: center;
    padding-bottom: 80px;
  }
  .random-section {
    width: calc(100% - 64px);
    margin: 0 auto;
  }
  .hero-text {
    margin-top: 18px;
    margin-bottom: 32px;
    width: 100%;
    font-size: 16px;
  }
}
.hero-swiper {
  margin-top: 40px;
}

.hero-swiper-wrapper {
  padding-bottom: 60px;
}

.hero-swiper-slide {
  text-align: center;
  font-size: 18px;
}

.hero-swiper-custom-pagination {
  position: absolute;
  left: 0;
}

.hero-swiper-pagination-bullet {
  width: 72px;
  border-radius: 0;
  height: 4px;
  background: rgba(185, 179, 179, 0.5);
  margin: 5px 0;
}

.hero-swiper-pagination-bullet-active:nth-child(1),
.hero-swiper-slide-active:nth-child(1) {
  --active-color: #34387F;
}
.hero-swiper-pagination-bullet-active:nth-child(2),
.hero-swiper-slide-active:nth-child(2) {
  --active-color: #600404;
}
.hero-swiper-pagination-bullet-active:nth-child(3),
.hero-swiper-slide-active:nth-child(3) {
  --active-color: #5B7F3C;
}

.hero-swiper-pagination-bullet-active {
  background-color: var(--active-color);
}

.hero-swiper-slide-active {
  color: var(--active-color);
}

.hero-swiper__container {
  height: 100%;
}

.hero-swiper__img1,
.hero-swiper__img2 {
  width: 220px;
  height: auto;
}

.hero-swiper__first-container {
  margin-bottom: 20px;
}

.hero-swiper__second-container {
  margin-left: auto;
  margin-right: 0;
  width: 220px;
  position: relative;
  background: linear-gradient(180deg, var(--active-color) 36.46%, rgba(52, 56, 127, 0) 85%);
}

.hero-swiper__text-effect {
  width: 80px;
  height: 80px;
  position: absolute;
  bottom: 178px;
  left: 14px;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

.hero-swiper__text {
  color: var(--active-color);
  color: #FAFAFA;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
  letter-spacing: -0.24px;
  text-transform: uppercase;
}

.hero-swiper__description-container {
  display: flex;
  position: relative;
  gap: 14px;
  width: 190px;
  margin-top: 14px;
  padding-top: 30px;
}
.hero-swiper__description-container::before {
  content: "";
  width: calc(100% - 20px);
  height: 6px;
  background-image: url(../images/vector.png);
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
}

.hero-swiper__description-title {
  color: #FAFAFA;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  letter-spacing: -0.24px;
}

.hero-swiper__description-text {
  color: rgba(250, 250, 250, 0.5);
  text-align: left;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  letter-spacing: -0.24px;
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .hero-swiper {
    width: 440px;
  }
  .hero-swiper__img1,
  .hero-swiper__img2 {
    width: 250px;
  }
  .hero-swiper__text-effect {
    bottom: 148px;
  }
  .hero-swiper__second-container,
  .hero-swiper__description-container {
    width: 250px;
  }
}
@media screen and (min-width: 768px) {
  .hero-swiper-slide {
    display: flex;
    justify-content: center;
    gap: 32px;
  }
  .hero-swiper-pagination-bullet {
    width: 100px;
  }
  .hero-swiper__first-container {
    margin-bottom: 0;
    height: auto;
    display: flex;
    align-items: flex-end;
  }
  .hero-swiper__second-container {
    width: auto;
    margin: 0;
  }
  .hero-swiper__img1,
  .hero-swiper__img2 {
    width: 336px;
  }
  .hero-swiper__text-effect {
    width: 130px;
    height: 130px;
    top: 380px;
    left: 16px;
  }
  .hero-swiper__text {
    font-size: 22px;
    line-height: 24px;
    letter-spacing: -0.48px;
  }
  .hero-swiper__description-container {
    gap: 55px;
    width: 320px;
  }
  .hero-swiper__description-container::before {
    width: 320px;
    height: 6px;
  }
}
@media screen and (min-width: 1200px) {
  .hero-swiper {
    width: 640px;
    margin-top: 0;
    position: absolute;
    right: 0;
    top: 0;
    height: 570px;
  }
  .hero-swiper__first-container {
    margin-bottom: 0;
    margin-left: 32px;
    display: flex;
    align-items: end;
    height: 100%;
  }
  .hero-swiper__second-container {
    margin-bottom: 0;
    width: 290px;
  }
  .hero-swiper__img1,
  .hero-swiper__img2 {
    width: 290px;
  }
  .hero-swiper__text-effect {
    width: 110px;
    height: 110px;
    top: 320px;
  }
  .hero-swiper__text {
    font-size: 20px;
  }
  .hero-swiper__description-container {
    gap: 30px;
    width: 280px;
  }
  .hero-swiper__description-container::before {
    width: 280px;
  }
  .hero-swiper-pagination-bullet {
    width: 4px;
    height: 100px;
  }
}
.random-title {
  margin: 40px 20px 35px;
  color: #FAFAFA;
  font-size: 26px;
  font-style: normal;
  font-weight: 500;
  line-height: 32px;
  letter-spacing: -0.56px;
  text-transform: uppercase;
}

.gallery {
  display: flex;
  align-items: center;
  column-gap: 40px;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
  margin-bottom: 30px;
}

.gallery__item {
  scroll-snap-align: center;
  padding-bottom: 20px;
}

.gallery__image {
  width: 100%;
  max-width: 400px;
  object-fit: cover;
}

.random-button {
  margin-left: 20px;
  border-radius: 1000px;
  border: 1px solid rgba(250, 250, 250, 0.3);
}

@media screen and (max-width: 480px) {
  .gallery__item {
    min-width: 75%;
  }
}
@media screen and (min-width: 768px) {
  .random-button {
    margin-left: 0;
  }
  .random-title {
    font-size: 44px;
    line-height: 44px;
    letter-spacing: -0.88px;
  }
  .random-section {
    width: 728px;
    margin-left: auto;
    margin-right: auto;
  }
  .gallery {
    column-gap: 50px;
  }
  .gallery__image {
    width: 300px;
  }
}
@media screen and (min-width: 1200px) {
  .random-section {
    width: 1136px;
    margin: 0 auto;
  }
  .hero-text {
    margin-top: 18px;
    margin-bottom: 32px;
    width: 100%;
    font-size: 16px;
  }
  .gallery {
    column-gap: 70px;
  }
  .gallery__image {
    width: 400px;
  }
  .random-title {
    margin-top: 70px;
  }
}
footer {
  width: calc(100% - 40px);
  margin: 40px auto;
}

.footer__logo {
  width: 98px;
  height: 15px;
  transition: transform cubic-bezier(0.075, 0.82, 0.165, 1) 250ms;
}
.footer__logo:hover, .footer__logo:focus {
  transform: scale(1.2);
}

.footer__description {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.footer__text {
  color: #FAFAFA;
  font-size: 14px;
  line-height: 18px;
}

.footer__icons {
  display: flex;
  gap: 14px;
}

.footer__icon {
  width: 32px;
  height: 32px;
  padding: 8px;
  border-radius: 6px;
  border: 1px solid rgba(250, 250, 250, 0.15);
  fill: #fff;
  transition: transform cubic-bezier(0.075, 0.82, 0.165, 1) 250ms;
}
.footer__icon:hover, .footer__icon:focus {
  transform: scale(1.2);
}

@media screen and (min-width: 480px) {
  footer {
    width: 440px;
  }
}
@media screen and (min-width: 768px) {
  footer {
    display: flex;
    justify-content: space-between;
    width: 704px;
    margin-bottom: 64px;
  }
  .footer__logo {
    width: 202px;
    height: 31px;
  }
  .footer__description {
    display: flex;
    gap: 142px;
    margin-bottom: 0;
  }
  .footer__text {
    font-size: 16px;
  }
  .footer__icons {
    gap: 16px;
  }
  .footer__icon {
    width: 40px;
    height: 40px;
    padding: 10px;
  }
}
.characters-header {
  position: relative;
  margin: 0 auto;
  padding: 25px 20px 20px 20px;
  width: 100%;
  height: 509px;
  background-image: url("../images/characters_mobile.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.characters-hero__subtitle {
  margin-top: 142px;
  font-size: 14px;
  line-height: 18px;
  text-align: left;
}

.characters-hero__title {
  width: auto;
  text-align: center;
  font-size: 50px;
  line-height: 60px;
  letter-spacing: -1.2px;
}

.characters-hero__text {
  margin: 16px auto 65px;
  width: calc(100% - 40px);
  max-width: 469px;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.32px;
}

.characters-hero__button {
  position: absolute;
  bottom: 20px;
  right: 20px;
  border-radius: 1000px;
  border: 1px solid rgba(250, 250, 250, 0.5);
}

@media screen and (min-width: 480px) {
  .characters-hero,
  .search-section {
    width: 440px;
    margin-left: auto;
    margin-right: auto;
  }
}
@media screen and (min-width: 768px) {
  .characters-header {
    background-image: url("../images/characters_tablet.png");
    height: 780px;
    padding: 32px;
  }
  .characters-hero,
  .search-section {
    width: 704px;
  }
  .characters-hero__subtitle {
    margin-top: 202px;
    font-size: 18px;
    line-height: 160%;
  }
  .characters-hero__title {
    margin-top: 27px;
    font-size: 128px;
    font-weight: 500;
    line-height: 128px;
    letter-spacing: -2.56px;
  }
}
@media screen and (min-width: 1200px) {
  .characters-header {
    background-image: url("../images/characters_desktop.png");
  }
  .characters-hero,
  .search-section {
    width: 1136px;
  }
  .characters-hero__title {
    text-align: left;
    margin-top: 16px;
    font-size: 150px;
    line-height: 150px;
    letter-spacing: -3px;
  }
  .characters-hero__text {
    margin-bottom: 200px;
    margin-left: 88px;
  }
  .characters-hero__button {
    bottom: 32px;
    right: 32px;
  }
}
.search-section {
  margin-top: 20px;
  width: calc(100% - 40px);
  margin-left: auto;
  margin-right: auto;
}

.search-form {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.search-form div {
  margin-bottom: 15px;
}

.search-form__text-filter {
  position: relative;
  width: 100%;
}

.search-form__filters {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.search-form__label {
  color: rgba(250, 250, 250, 0.3);
  font-size: 12px;
  line-height: 14px;
  margin-left: 10px;
}

.search-form__input {
  margin-top: 8px;
  width: 100%;
  color: #FAFAFA;
  font-size: 14px;
  line-height: 18px;
  background-color: transparent;
  border: 2px solid #34387F;
  padding: 14px 24px;
  border-radius: 100px;
}
.search-form__input::placeholder {
  color: #FAFAFA;
}

.search-form__text-input {
  padding: 16px 60px 16px 24px;
  cursor: text;
}
.search-form__text-input:focus {
  border: none;
  background-color: rgba(52, 56, 127, 0.4666666667);
  outline: none;
}

.search-form__button {
  position: absolute;
  background-color: transparent;
  border: none;
  width: 40px;
  height: 40px;
  right: 18px;
  top: 35px;
}

div.dropdown-input {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0;
}

.dropdown-text {
  color: #FAFAFA;
  font-size: 14px;
  line-height: 18px;
}

.dropdown-toggle {
  background-color: transparent;
  border: none;
}

.dropdown-list {
  position: absolute;
  width: 100%;
  display: none;
  padding: 14px 24px;
  border-radius: 16px;
  background: #171717;
}
.dropdown-list.show {
  display: block;
}

.dropdown-item {
  cursor: pointer;
  color: rgba(250, 250, 250, 0.3);
  font-size: 12px;
  line-height: 14px;
}
.dropdown-item:not(:last-child) {
  margin-bottom: 8px;
}
.dropdown-item:hover, .dropdown-item.active {
  color: #FAFAFA;
}

.search-form__date-filter {
  position: relative;
}

.search-form__input.search-form__date-input.form-control,
.search-form__date-input {
  cursor: text;
  height: 57px;
}
.search-form__input.search-form__date-input.form-control:focus,
.search-form__date-input:focus {
  outline: none;
}

span.flatpickr-day.selected, .flatpickr-day.selected.inRange {
  background: #bcbdd1;
  border-color: #bcbdd1;
}
span.flatpickr-day.selected:hover, span.flatpickr-day.selected:focus, .flatpickr-day.selected.inRange:hover, .flatpickr-day.selected.inRange:focus {
  background: #bcbdd1;
  border-color: #bcbdd1;
}

@media screen and (min-width: 375px) {
  .search-form__text-filter {
    width: 345px;
  }
  .search-form__select-filter {
    width: 169px;
  }
  .search-form__date-filter {
    width: 158px;
  }
  .dropdown-text {
    font-size: 16px;
    line-height: 18px;
  }
  .search-form__select {
    width: 170px;
  }
}
@media screen and (min-width: 480px) {
  .search-form {
    width: 440px;
    margin-left: auto;
    margin-right: auto;
  }
}
@media screen and (min-width: 768px) {
  .search-form {
    width: 704px;
  }
  .search-form__text-filter {
    width: 170px;
  }
  .search-form__label {
    font-size: 14px;
    line-height: 18px;
  }
  .search-form__input {
    font-size: 16px;
    line-height: 18px;
    padding: 16px 24px;
    height: 62px;
  }
  .dropdown-list {
    padding: 16px 24px;
  }
  .dropdown-item {
    font-size: 16px;
    line-height: 18px;
  }
  .search-form {
    gap: 8px;
  }
  .search-form__button {
    position: absolute;
    background-color: transparent;
    border: none;
    right: 18px;
    top: 39px;
  }
  .search-form__input.search-form__date-input.form-control,
  .search-form__date-input {
    height: 62px;
  }
}
@media screen and (min-width: 1200px) {
  .search-form {
    width: 1136px;
  }
  .search-form__text-filter {
    width: 260px;
  }
  .search-form {
    gap: 16px;
  }
}
.card-section {
  width: calc(100% - 40px);
  margin-left: auto;
  margin-right: auto;
}

.cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 35px;
}

.card {
  width: 335px;
  transition: transform 450ms cubic-bezier(0.075, 0.82, 0.165, 1);
}
.card:hover, .card:focus {
  transform: scale(1.05);
}

.card__img {
  width: 100%;
  height: 445px;
  border-radius: 8px;
  object-fit: cover;
}

.card__name {
  margin-top: 16px;
  color: #FAFAFA;
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.36px;
}

.card-notification {
  position: relative;
  margin: 40px auto 120px;
  height: 152px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-notification__svg {
  position: absolute;
  width: 257px;
  height: 140px;
  transform: rotate(4deg);
  flex-shrink: 0;
  stroke-width: 3px;
  stroke: #34387F;
}

.card-notification__text {
  text-align: center;
  width: 151px;
  color: #FAFAFA;
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.36px;
}

.card__empty-container {
  height: 50vh;
  width: 100%;
}

@media screen and (min-width: 768px) {
  .card-section {
    width: 704px;
  }
  .cards {
    gap: 27px;
  }
  .card__name {
    margin-top: 14px;
    font-size: 24px;
    letter-spacing: -0.48px;
  }
  .card-notification {
    margin: 100px auto 195px;
    height: 220px;
  }
  .card-notification__svg {
    width: 363px;
    height: 197px;
    stroke-width: 4px;
  }
  .card-notification__text {
    width: 192px;
    font-size: 24px;
    line-height: 28px;
    letter-spacing: -0.48px;
  }
}
@media screen and (min-width: 1200px) {
  .card-section {
    width: 1136px;
  }
  .cards {
    gap: 27px;
    row-gap: 64px;
  }
  .card {
    width: 330px;
  }
  .card__img {
    width: 100%;
    object-fit: cover;
  }
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.2);
  opacity: 1;
  transition: opacity 250ms cubic-bezier(0.075, 0.82, 0.165, 1);
  overflow: hidden;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.modal-window {
  width: calc(100% - 40px);
  padding: 48px 20px 40px;
  height: auto;
  max-height: 80vh;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%);
  background: #0C0B0B;
  overflow-y: auto;
  scrollbar-color: #5c5a5a;
}
.modal-window::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #0C0B0B;
}
.modal-window::-webkit-scrollbar {
  width: 6px;
  background-color: #0C0B0B;
}
.modal-window::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #2a2a2a;
}

.modal-window__button {
  position: absolute;
  right: 8px;
  top: 8px;
  width: 30px;
  height: 30px;
  margin: 0;
  padding: 0;
  text-align: center;
  border: none;
  transform: scale(1);
  background-color: transparent;
  transition: transform 250ms cubic-bezier(0.075, 0.82, 0.165, 1);
}
.modal-window__button:hover {
  transform: scale(1.25);
}

.modal-card {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}

